.moon {
	background-color: #222;
	width: 50px;
	height: 50px;
	border-radius: 50%;
	border: 4px solid #222;
	/* I set 2 shadows where the first one doesn't have blur and spread for firefox fix. */
	box-shadow: inset 0px 8px #999, inset 0px 8px 1px 1px #999;
	-moz-box-shadow: inset 0px 8px #999, inset 0px 8px 1px 1px #999;
}
